// ------------------------------
// MEDIA QUERIES
// ------------------------------

@media all and (min-width: 1300px) {

}

// ------------------------------
// < 1100px
// ------------------------------

@media all and (max-width: 1100px) {

  .panel-cover__logo {
    width: 70px;
  }

  .panel-title {
    font-size: 2em;
  }

  .panel-subtitle {
    font-size: 1em;
  }

  .panel-cover__description {
    margin: 0 10px;
    font-size: .9em;
  }

  .navigation--social {
    margin-top: 5px;
    margin-left: 0;
  }

}
@media all and (min-width: $wildmeowth-pad-width) {
  .panel-cover--collapsed {
    .navigation__item {
      a{
        border: none;
      }
    }
    .navigation-items {
      position: absolute;
      top: 0;
      right: 20px;
    }
    .navigation-social{
      display: none;
    }
  }
}
// ------------------------------
// < 991px
// ------------------------------

@media all and (max-width: $wildmeowth-pad-width) {

  .btn-mobile-menu {
    display: block;
  }

  .panel-main {
    display: table;
    position: relative;
  }

  .panel-cover{
    background-position: 50% 0%;
    &.panel-cover--collapsed {
      width: 100%;
      max-width: none;
      min-height: 0;
      height: auto;
      background-position: 0% 82%;
      .panel-main__inner {
        padding: 70px 0 40px 0;
      }
    }
  }
  .panel-title {
    margin-top: 10px;
  }

  .panel-main__inner {
    display: table-cell;
    padding: 60px 10%;
  }

  .panel-cover__description {
    display: block;
    max-width: 600px;
    padding: 0 20px;
    margin: 0 auto;
  }

  .panel-cover__divider--secondary {
    display: none;
  }

    .navigation-wrapper {
      display: none;
      position: fixed;
      top: 0;
      right: 0;
      left: 0;
      width: 100%;
      padding: 20px 0;
      background: rgba($wildmeowth-gray-darkest,.98);
      border-bottom: 1px solid rgba(255,255,255,.15);

      &.visible {
        display: block;
      }
    }

    .cover-navigation {
      display: block;
      position: relative;
      float: left;
      clear: left;
      width: 100%;

      .navigation {
        display: block;
        width: 100%;

        li {
          width: 80%;
          margin-bottom: .4em;
        }

      }

      &.navigation--social {
        padding-top: 5px;

        .navigation {

          li {
            display: inline-block;
            width: 25.8%;
          }

        }

      }

    }

    .content-wrapper {
      width: 80%;
      max-width: none;
      margin: 0 auto;
    }

    .content-wrapper__inner {
      margin-right: 0;
      margin-left: 0;
    }

    .navigation__item {
      width: 100%;
      margin: 0 0 .4em 0;
    }

}

@media all and (max-width: $wildmeowth-phone-width) {
  .panel-main__inner {
    padding: 0 5%;
  }

  .panel-title {
    margin-bottom: .1em;
    font-size: 1.5em;
  }

  .panel-subtitle {
    font-size: .9em;
  }
  .panel-cover{
    &.panel-cover--collapsed {
      background-position: 0% 88%;
    }
  }
}