// ------------------------------
// SINGLE POST LAYOUT
// ------------------------------

.post-comments {
  border-top: 1px solid $wildmeowth-gray-light;
  padding: 60px 0;
}

// ------------------------------
// SINGLE POST METAß
// ------------------------------

.post-meta {
  margin: 0 0 1.5em 0;
  color: darken($wildmeowth-gray, 2%);
  .tags{
    color: $wildmeowth-white;
    background-color: $wildmeowth-blue;
    padding: 1px 5px;
    border-radius: 15px;
    @media (max-width: $wildmeowth-sm-width) {
      padding: 1px;
    }
  }
  .categories{
    background-color: $wildmeowth-gray-dark;
  }
}

  .post-meta__date {
    margin-right: .5em;
  }

  .post-meta__tags {
    margin-left: .4em;
  }

  .post-meta__author {
    margin-left: 1.5em;
  }

  .post-meta__avatar {
    display: inline-block;
    width: 22px;
    height: 22px;
    margin: 0 .3em -.4em 0;
    border: none;
    box-shadow:none;
  }

// ------------------------------
// SINGLE POST STYLES
// ------------------------------

.post {

  img {
    max-width: 100%;
    margin: 0 auto;
    border-radius: $wildmeowth-border-radius;
    text-align: center;
    display: block;
  }

  pre {
    overflow: auto;
  }

  hr {
    display: block;
    width: 30%;
    margin: 2em 0;
    border-top: 1px solid $wildmeowth-gray-light;
  }

}
